import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Content } from 'ionic-angular';

import axios from 'axios';

import { AppConfig } from '../../app/app.config';

/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  
  public name: string = '';
  public email: string = '';
  public password: string = '';
  public password_confirmation: string = '';

  public registerUrl = AppConfig.getUrl() + '/api/register';

  public registerFail = false;
  public registerFailMsg = 'Please type correct information!';

  constructor(public navCtrl: NavController, public navParams: NavParams) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPage');
  }

  onRegisterButtonClick()
  {
    if (this.name.trim() == '' || this.email.trim() == '' || this.password.trim() == '' || this.password != this.password_confirmation) {
      this.registerFail = true;
      return;
    }

    this.navCtrl.pop();
  }

}



